.contact-form {
  width: 600px;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px;
  display: flex;
  box-shadow: 0 10px 10px #ccc;
}

.contact-form-input {
  height: 40px;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding-left: 15px;
}

.contact-form-message {
  height: 120px;
  resize: none;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding: 15px;
}

.contact-form-submit {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #303030;
  border-radius: 4px;
  align-self: center;
  padding: 10px 20px;
  transition: transform .25s linear;
}

.contact-form-submit:hover, .contact-form-submit:focus {
  transform: scale(1.05);
}

